<template>
  <page-content>
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="24">
          <a-col :md="6" :sm="12">
            <a-form-item label="关键字">
              <a-input v-model="queryParams.keywords" placeholder="输入关键字" style="width: 250px" />
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="12">
            <a-form-item label="在线状态">
              <a-select
                v-model="queryParams.status"
                placeholder="请选择"
                default-value="0"
                style="width: 200px"
              >
                <a-select-option value="">全部</a-select-option>
                <a-select-option v-for="(value, key) in statusItems" :key="key" :value="key">{{
                  value
                }}</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="12">
            <a-button type="primary" @click="refresh(false)">查询</a-button>
            <a-button style="margin-left: 8px" @click="resetSearchForm()">重置</a-button>
          </a-col>
        </a-row>
      </a-form>
    </div>
    <div class="table-operator">
      <div class="batch-control pull-left">
        <!-- 可以使用自定义指令进行按钮鉴权限  -->
        <!-- 后端对操作权限可以固定设置：增、删、改、查  -->
        <!-- 后端对操作权限也可以详细设置：增、删、改、查、封禁、解封、批量封禁、批量解封  -->
        <a-dropdown v-if="selectedRowKeys.length > 0">
          <a-menu slot="overlay">
            <!-- lock | unlock -->
            <a-menu-item key="1" @click="batchUpdateMediaServer()">
              <a-icon type="setting" />更换流媒体服务节点
            </a-menu-item>
            <a-menu-item key="2" @click="batchActiveLiveCameras()">
              <a-icon type="setting" />激活设备
            </a-menu-item>
            <a-menu-item key="3" @click="batchDeleteCameras()"> <a-icon type="delete" />删除 </a-menu-item>
            <a-menu-item key="4" @click="batchLockedCameras()"> <a-icon type="lock" />封禁 </a-menu-item>
            <a-menu-item key="5" @click="batchRefreshCameras()"> <a-icon type="sync" />同步 </a-menu-item>
            <a-menu-item key="6" @click="batchOpenLiveCameras()">
              <a-icon type="play-circle" />开启自动推流直播
            </a-menu-item>
            <a-menu-item key="7" @click="batchStopLiveCameras()">
              <a-icon type="close" />关闭直播
            </a-menu-item>
          </a-menu>
          <a-button style="margin-left: 8px">
            批量操作
            <a-icon type="down" />
          </a-button>
        </a-dropdown>
      </div>
    </div>
    <a-table
      :columns="columns"
      :data-source="ipcDevices"
      :pagination="false"
      :rowSelection="rowSelection"
      ref="table"
      :loading="loading"
      rowKey="id"
    >
      <template slot="channel_name" slot-scope="text, record">
        <edit-table-cell :text="text" @change="onCellChange(record.id, 'channel_name', $event)" />
      </template>
      <template slot="media_server_id" slot-scope="media_server_id, record">
        <span v-if="record.close_live == 1" style="color: red">推流已关闭</span>
        <span v-else>{{ media_server_id }}</span>
      </template>
      <template slot="time" slot-scope="time, record">
          <div class="time-show"><label for="">创建:</label><span>{{time | filterTime(time)}}</span></div>
          <div class="time-show" v-if="record.lastOnlineTime != 0"><label for="">上线:</label><span>{{record.lastOnlineTime | filterTime(record.lastOnlineTime)}}</span></div>
          <div class="time-show" v-if="record.lastOfflineTime != 0"><label for="">离线:</label><span>{{record.lastOfflineTime | filterTime(record.lastOfflineTime)}}</span></div>
      </template>
      <template slot="status" slot-scope="status, record">
        <a-badge :status="status | statusTypeFilter" :text="record.device_status_title" />
      </template>
    </a-table>
    <lz-pagination :page="paginator" @change="changePaginator" @showSizeChange="showSizeChange" :autoPush="false" />
    <bind-form
      ref="bindFormModal"
      :visible="visible"
      :loading="confirmLoading"
      @cancel="handleCancel"
      @ok="handleOk"
    />
  </page-content>
</template>

<script>
import {
    getIpcDevices,
    getStatusItems,
    batchDeleteIpc,
    editIpc,
    batchSyncIpc,
    batchActiveIpc,
    batchLockIpc,
    batchOpenLiveIpc,
    batchCloseLiveIpc,
    batchUpdateMediaServer
} from '@/api/device'
import Space from '@c/Space'
import LzPagination from '@c/LzPagination'
import PageContent from '@c/PageContent'
import LzPopconfirm from '@c/LzPopconfirm'
import { getRouteByCode } from '@/libs/utils'
import { EditTableCell } from '@c/'
import moment from 'moment'
const defaultQueryParams = {
    keywords: '',
    status: '',
    recorderId: '',
    parterId: '',
    enabled: ''
}
export default {
    name: 'Index',
    scroll: true,
    components: {
        LzPopconfirm,
        PageContent,
        LzPagination,
        Space,
        EditTableCell,
        bindForm: () => import('./setting-media-server.vue')
    },
    data () {
        return {
            statusItems: [],
            visible: false,
            confirmLoading: false,
            loading: false,
            queryParams: defaultQueryParams,
            selectedRowKeys: [],
            selectedRows: [],
            columns: [
                {
                    title: '设备编号',
                    dataIndex: 'main_id',
                    key: 'main_id',
                    width: 120
                },
                {
                    title: '通道名称',
                    dataIndex: 'channel_name',
                    key: 'channel_name',
                    width: 250,
                    scopedSlots: { customRender: 'channel_name' }
                },
                {
                    title: '通道ID',
                    dataIndex: 'channel_id',
                    key: 'channel_id',
                    ellipsis: true
                },
                {
                    title: 'IP',
                    dataIndex: 'ip_v4_address',
                    key: 'ip_v4_address'
                },
                {
                    title: '流媒体服务器',
                    dataIndex: 'media_server_id',
                    key: 'media_server_id',
                    ellipsis: true,
                    scopedSlots: { customRender: 'media_server_id' }
                },
                {
                    title: '在线状态',
                    dataIndex: 'device_status',
                    key: 'device_status',
                    ellipsis: true,
                    scopedSlots: { customRender: 'status' }
                },
                {
                    title: '激活状态',
                    dataIndex: 'enabled_title',
                    key: 'enabled_title',
                    ellipsis: true
                },
                {
                    title: '添加时间',
                    dataIndex: 'createdTime',
                    key: 'createdTime',
                    width: 250,
                    ellipsis: true,
                    scopedSlots: { customRender: 'time' }
                }
            ],
            ipcDevices: [],
            paginator: null,
            hasEdit: false,
            hasDelete: false
        }
    },
    mounted () {
        this.getStatusItems()
        this.loadData()
        this.checkPermissions()
    },
    computed: {
        rowSelection () {
            return {
                selectedRowKeys: this.selectedRowKeys,
                onChange: this.onSelectChange
            }
        },
        selectedUnActiveRowKeys () {
            if (!this.selectedRows) {
                return []
            }

            const keys = this.selectedRows
                .filter((row) => row.enabled == 0)
                .map((item, index) => {
                    return item.id
                })

            return keys
        },
        selectedActiveRowKeys () {
            if (!this.selectedRows) {
                return []
            }

            const keys = this.selectedRows
                .filter((row) => row.enabled === 1)
                .map((item, index) => {
                    return item.id
                })

            return keys
        }
    },
    methods: {
        getPopupContainer (trigger) {
            return trigger.parentElement
        },
        checkPermissions () {
            const vueRouters = this.$store.state.vueRouters.vueRouters
            let route = getRouteByCode(vueRouters, 'device-ipc-edit')
            if (route) {
                this.hasEdit = true
            }
            route = getRouteByCode(vueRouters, 'device-ipc-lock')
            if (route) {
                this.hasDelete = true
            }
        },
        onSelectChange (selectedRowKeys, selectedRows) {
            this.selectedRowKeys = selectedRowKeys
            this.selectedRows = selectedRows
        },
        onCellChange (key, dataIndex, value) {
            if (!value) {
                return false
            }

            const formData = { channel_name: value, scenario: 'changeChannelName' }
            editIpc(key, formData)
                .then((response) => {
                    this.refresh()
                })
                .catch((error) => {
                    this.$message.error(error.message || '修改出错了')
                })
        },
        handleEdit (id) {},
        handleCancel () {
            this.visible = false
        },
        handleOk () {
            const model = this.$refs.bindFormModal.model
            this.confirmLoading = true
            this.$refs.bindFormModal.$refs.bindForm.validate((valid) => {
                if (!valid) {
                    return false
                }
                batchUpdateMediaServer(this.selectedRowKeys, model.media_server_id).then((response) => {
                    this.$message.success('保存成功')
                    this.visible = false
                    this.confirmLoading = false
                    this.refresh()
                })
            })
        },
        resetSearchForm () {
            this.queryParams = defaultQueryParams
        },
        refresh (resetSearch = true) {
            this.loadData()
            if (resetSearch) {
                this.resetSearchForm()
            }
            this.resetTableSelection()
        },
        showSizeChange (current, perPage) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: current,
                limit: perPage
            })
            this.refresh()
        },
        changePaginator (page, pageSize) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: page,
                limit: pageSize
            })
            this.refresh()
        },
        async getStatusItems () {
            this.statusItems = await getStatusItems()
        },
        async loadData () {
            this.loading = true
            if (this.$route.params.hasOwnProperty('id')) {
                this.queryParams.recorderId = this.$route.params.id
            }

            const { devices, paginator } = await getIpcDevices(this.queryParams)
            this.ipcDevices = devices
            this.paginator = paginator
            this.loading = false
        },
        dateFormat (row, column) {
            var date = row[column.property]
            console.log('date:', date)
            if (!date) {
                return ''
            }
            return moment(date).format('YYYY-MM-DD HH:mm:ss')
        },
        async batchDeleteCameras () {
            const that = this
            this.$confirm({
                title: '删除不能恢复，确认要删除选中的设备吗?',
                async onOk () {
                    const reuslt = await batchDeleteIpc(that.selectedRowKeys)
                    that.$message.success('删除成功')
                    that.refresh()
                },
                onCancel () {},
                class: 'test'
            })
        },
        async batchLockedCameras () {
            const that = this
            this.$confirm({
                title: '确认要封禁选中的设备吗?',
                async onOk () {
                    const reuslt = await batchLockIpc(that.selectedRowKeys)
                    that.$message.success('封禁成功')
                    that.refresh()
                },
                onCancel () {},
                class: 'test'
            })
        },
        async batchActiveLiveCameras () {
            const that = this
            this.$confirm({
                title: '确认要激活选中的设备吗?',
                async onOk () {
                    const reuslt = await batchActiveIpc(that.selectedUnActiveRowKeys)
                    if (reuslt.async) {
                        that.$message.success('任务已生成，可等待1分钟刷新页面查看')
                    } else {
                        that.$message.success('开启成功')
                    }
                    that.refresh()
                },
                onCancel () {},
                class: 'test'
            })
        },
        async batchOpenLiveCameras () {
            const that = this
            this.$confirm({
                title: '确认要开启选中的设备的直播吗?',
                async onOk () {
                    const reuslt = await batchOpenLiveIpc(that.selectedActiveRowKeys)
                    if (reuslt.async) {
                        that.$message.success('任务已生成，可等待1分钟刷新页面查看')
                    } else {
                        that.$message.success('关闭成功')
                    }
                    that.refresh()
                },
                onCancel () {},
                class: 'test'
            })
        },
        async batchStopLiveCameras () {
            const that = this
            this.$confirm({
                title: '确认要关闭选中的设备的直播吗?',
                async onOk () {
                    const reuslt = await batchCloseLiveIpc(that.selectedActiveRowKeys)
                    if (reuslt.async) {
                        that.$message.success('任务已生成，可等待1分钟刷新页面查看')
                    } else {
                        that.$message.success('关闭成功')
                    }
                    that.refresh()
                },
                onCancel () {},
                class: 'test'
            })
        },
        async batchRefreshCameras () {
            const that = this
            this.$confirm({
                title: '确认要同步选中的设备吗?',
                async onOk () {
                    const reuslt = await batchSyncIpc(that.selectedRowKeys)
                    if (reuslt.async) {
                        that.$message.success('同步任务已生成，可等待1分钟刷新页面查看')
                    } else {
                        that.$message.success('同步成功')
                    }
                    that.refresh()
                },
                onCancel () {},
                class: 'test'
            })
        },
        batchUpdateMediaServer () {
            this.$refs.bindFormModal.resetForm()
            this.visible = true
            this.confirmLoading = false
        },
        resetTableSelection () {
            this.selectedRowKeys = []
            this.selectedRows = []
        }
    },
    filters: {
        filterTime (time) {
            return moment(time).format('YYYY-MM-DD HH:mm')
        },
        statusTypeFilter (type) {
            const statusTypeMap = {
                0: 'warning',
                '-1': 'error',
                1: 'success'
            }
            return statusTypeMap[type]
        }
    }
}
</script>
<style lang="less">
.del-span {
    text-decoration: line-through;
    color: red;
}
.pwd-border-none {
    .ant-input {
        border: none;
        padding-left: 0px;
        &:focus {
            border: none;
        }
    }
}
.time-show {
    font-size: 14px;
}
</style>
